
import { defineComponent } from 'vue'
import { COM20000111_REQ_BODY, COM20000111_RES, CheckBookRequest, MyApproval, MyRequest, BankerCheckRequest } from '@/shared/type/COM20000111.type'
import { DataCenter } from '@/shared/services/DataCenter'

export default defineComponent({
	data() {
		return {
			checkBookRequest: [] as CheckBookRequest[],
			myApproval: [] as MyApproval[],
			myRequest: [] as MyRequest[],
			bankerCheckRequest: [] as BankerCheckRequest[],
		}
	},
	mounted() {
		this.onRetrieveDashboard();
	},
	methods: {
		async onRetrieveDashboard() {
			const reqBody: COM20000111_REQ_BODY = {
				employeeNo: await DataCenter.getData('employeeNo') as string || '',
			}
			const responseData = await this.$request.getRequest('COM20000111', reqBody, false) as COM20000111_RES
			if (responseData.header.result) {
				this.checkBookRequest = responseData.body.checkBookRequest;
				this.myApproval = responseData.body.myApproval;
				this.myRequest = responseData.body.myRequest;
				this.bankerCheckRequest = responseData.body.requestBankerCheck;
			}
		},

		goToMyRequest(requestType: 'approving' | 'approved' | 'returned' | 'rejected') {
			this.$router.push({
				path: '/APM202001SC00',
				query: {
					filter: requestType,
					tab: 'my-request'
				}
			})
		},

		goToMyApproval(requestType: 'need-approval', status: string) {
			this.$router.push({
				path: '/APM202001SC00',
				query: {
					filter: requestType,
					status: status,
					tab: 'my-approval'
				}
			})
		},

		goToCheckBookRequest(requestType: 'register' | 'processing' | 'completed') {
			this.$router.push({
				path: '/DCM201001SC00',
				query: {
					filter: requestType,
				}
			})
		},

		goToBankerCheckRequest(requestType: 'register' | 'processing' | 'completed') {
			this.$router.push({
				path: '/DCM201002SC00',
				query: {
					filter: requestType,
				}
			})
		}
	}
})
